.button {
  position: relative;

  box-sizing: border-box;

  border: 0;
  border-radius: 22px;

  padding: 0 0 0 1px;

  background: transparent;

  font-family: montserrat, Arial, sans-serif;
  color: #BC9CFF;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;

  cursor: pointer;
}

.button_width_ultra-narrow {
  width: 61px;
}

.button_width_narrow {
  width: 87px;
}

.button_width_regular {
  width: 99px;
}

.button_width_wide {
  width: 196px;
}

.button_width_ultra-wide {
  width: 320px;
}

@media all and (max-width: 400px) {
  .button_width_ultra-wide {
    width: 266px;
  }
}

.button_height_low {
  height: 34px;

  line-height: 34px;
}

.button_height_regular {
  height: 44px;

  line-height: 44px;
}

.button_bordered {
  border: 2px solid transparent;

  background: white;
}

.button_bordered::before {
  content: '';

  box-sizing: border-box;

  border: 0;
  border-radius: 22px;

  background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);

  width: inherit;
  height: inherit;
}
